.image-wrapper{
    position: relative;
    
    border: 2px solid #B0855B;
    box-sizing: border-box;
    border-radius: 140px 0px;

}

.image-radius {
    position: absolute;
    right: 32px;
    bottom: 32px;
    border-radius: 140px 0px;
}

.image-radius--right {
    left: 34px;
    top: 32px;
}